.formWrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    z-index: 19999;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
}

.formContainer {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.98);
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 800px;
    max-height: 85vh;
    overflow-y: auto;
    z-index: 20000;
    animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0.95);
    }

    to {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

.formContainer::-webkit-scrollbar {
    width: 8px;
}

.formContainer::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.formContainer::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 4px;
}

.formContainer::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.formContainer .MuiFormControl-root {
    margin-bottom: 10px;
}

.formContainer .MuiInputBase-root {
    font-size: 0.9rem;
}

.formContainer .MuiInputLabel-root {
    font-size: 0.9rem;
}

.guestInfoContainer {
    grid-column: span 2;
}

.roomTypeContainer,
.paymentMethodContainer {
    grid-column: span 1;
}

.commentsStyles {
    grid-column: span 3;
}

input[type="submit"] {
    grid-column: span 3;
    width: 100%;
    padding: 10px;
    margin-top: 10px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
}

.deleteButton {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    margin-right: 10px;
    transition: background-color 0.3s ease;
}

.deleteButton:hover {
    background-color: #d32f2f;
}

.submit-button-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 20px;
}

input[type="submit"]:hover {
    background-color: #45a049;
}

.span-2 {
    grid-column: span 2;
}

.span-3 {
    grid-column: span 3;
}

.personalInput {
    width: 100%;
}

button[aria-label="Close form"] {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    color: #888;
}

button[aria-label="Close form"]:hover {
    color: #555;
}

@media screen and (max-width: 768px) {
    .formContainer {
        grid-template-columns: 1fr 1fr;
        width: 95%;
        padding: 15px;
        gap: 10px;
    }
}

@media screen and (max-width: 480px) {
    .formContainer {
        grid-template-columns: 1fr;
    }

    .guestInfoContainer,
    .roomTypeContainer,
    .paymentMethodContainer,
    .commentsStyles,
    input[type="submit"] {
        grid-column: span 1;
    }
}

.date-nights-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
    margin-bottom: 15px;
}

@media (max-width: 768px) {
    .date-nights-container {
        grid-template-columns: 1fr;
    }
}

.status-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;
    margin-bottom: 20px;
}

.comments-container {
    grid-column: span 3;
}

/* Style for select inputs */
.MuiFormControl-root .MuiInputLabel-root {
    background-color: white;
    padding: 0 5px;
    margin-left: -5px;
}

.MuiFormControl-root .MuiOutlinedInput-notchedOutline {
    border-color: #bdbdbd;
}

.MuiFormControl-root:hover .MuiOutlinedInput-notchedOutline {
    border-color: #757575;
}

.MuiFormControl-root.Mui-focused .MuiOutlinedInput-notchedOutline {
    border-color: #1976d2;
}

@media (max-width: 768px) {
    .formContainer {
        grid-template-columns: 1fr;
    }

    .date-nights-container,
    .status-container {
        grid-template-columns: 1fr;
    }
}

.formColumn {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.full-width-input {
    width: 100%;
}

.date-nights-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;
    margin-bottom: 15px;
}

.date-nights-container .full-width-input {
    width: 100%;
}

@media (max-width: 768px) {
    .formContainer {
        grid-template-columns: 1fr;
    }

    .date-nights-container {
        grid-template-columns: 1fr;
    }

    .full-width-input {
        width: 100%;
    }
}