.room-numbers-container {
    display: grid;
    grid-template-rows: repeat(15, minmax(5px, auto));
    grid-auto-rows: 1fr;
    width: 100%;
    height: 75vh;
    background: linear-gradient(180deg, #f8f9fa, #e9ecef);
    /* Colores más claros */
    box-shadow: 2px 0 4px rgba(0, 0, 0, 0.1);
}

.room-number-cell {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    font-weight: 600;
    color: #495057;
    /* Color de texto más oscuro para contraste */
    background: linear-gradient(145deg, #ffffff, #f8f9fa);
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    border-right: 1px solid rgba(0, 0, 0, 0.05);
    padding: 8px;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

.room-number-cell:nth-child(even) {
    background: linear-gradient(145deg, #f8f9fa, #f1f3f5);
}

.room-number-cell:hover {
    background: linear-gradient(145deg, #e9ecef, #dee2e6);
    color: #228be6;
    /* Azul más claro */
    transform: translateX(2px);
    box-shadow:
        inset -2px 0 4px rgba(34, 139, 230, 0.1),
        inset 2px 0 4px rgba(34, 139, 230, 0.1);
}

.room-number-cell::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg, transparent, #228be6, transparent);
    opacity: 0;
    transition: opacity 0.3s ease;
}

.room-number-cell:hover::before {
    opacity: 0.3;
}