.inputFormContainer {
    position: relative;
    margin-bottom: 16px;
}

.dateInput input {
    font-size: 16px;
}

.errorContainer {
    min-height: 20px;
    /* Reserve space for error message */
}

.errorMessage {
    color: red;
    animation: fadeIn 0.5s ease-in-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(-10px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.custom-date-input {
    display: flex;
    flex-direction: column;
}

.custom-date-input label {
    margin-bottom: 5px;
    font-size: 0.9rem;
    color: rgba(0, 0, 0, 0.6);
}

.custom-date-input input[type="date"] {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    width: 100%;
    box-sizing: border-box;
}

.custom-date-input input[type="date"]:focus {
    outline: none;
    border-color: #1976d2;
    box-shadow: 0 0 0 2px rgba(25, 118, 210, 0.2);
}

.error-message {
    color: #f44336;
    font-size: 0.75rem;
    margin-top: 3px;
}

.mui-date-input-wrapper {
    width: 100%;
    margin-bottom: 0;
    /* Remove bottom margin */
}

.mui-date-input-wrapper .MuiTextField-root {
    width: 100%;
}

.mui-date-input-wrapper .MuiInputBase-root {
    height: 56px;
    border: 1px solid rgba(0, 0, 0, 0.23);
    border-radius: 4px;
    cursor: pointer;
    padding: 0;
    /* Remove padding */
}

.mui-date-input-wrapper .MuiInputBase-root:hover {
    border-color: rgba(0, 0, 0, 0.87);
}

.mui-date-input-wrapper .MuiInputBase-root.Mui-focused {
    border-color: #1976d2;
    border-width: 2px;
    box-shadow: 0 0 0 2px rgba(25, 118, 210, 0.2);
}

.mui-date-input-wrapper .MuiOutlinedInput-notchedOutline {
    border: none;
}

.mui-date-input-wrapper input[type="date"] {
    font-size: 16px;
    padding: 0 14px;
    /* Keep horizontal padding, remove vertical padding */
    border: none;
    background: transparent;
    cursor: pointer;
}

.mui-date-input-wrapper input[type="date"]::-webkit-calendar-picker-indicator {
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
}

.mui-date-input-wrapper .MuiInputAdornment-root {
    margin-right: 8px;
    color: rgba(0, 0, 0, 0.54);
    pointer-events: none;
}

/* Ensure the label doesn't interfere with clicking */
.mui-date-input-wrapper .MuiInputLabel-root {
    pointer-events: none;
}

/* Add these new styles */
.mui-date-input-wrapper .MuiFormControl-root {
    width: 100%;
}

.mui-date-input-wrapper .MuiInputBase-root {
    width: 100%;
}

/* Adjust the label position */
.mui-date-input-wrapper .MuiInputLabel-root {
    transform: translate(14px, 16px) scale(1);
}

.mui-date-input-wrapper .MuiInputLabel-shrink {
    transform: translate(14px, -6px) scale(0.75);
}

.MuiPopover-root,
.MuiMenu-paper {
    position: absolute !important;
}

.formContainer {
    position: relative;
    z-index: 20000;
    max-height: 85vh;
    /* Limit the height to 85% of the viewport height */
    overflow-y: auto;
    /* Enable vertical scrolling */
    padding-right: 17px;
    /* Compensate for the scrollbar width */
    box-sizing: content-box;
    /* Ensure padding doesn't affect the width */
}

body {
    overflow: hidden;
    /* Prevent body scrolling when form is open */
    padding-right: 0 !important;
}

.MuiButtonGroup-root {
    height: 56px;
    /* Match the height of other inputs */
}

.MuiInputBase-root,
.MuiButtonGroup-root {
    height: 56px !important;
}

/* Add these new styles at the end of the file */

.MuiPopover-root,
.MuiMenu-root {
    position: fixed !important;
    z-index: 21000 !important;
}

.MuiPopover-paper,
.MuiMenu-paper {
    position: absolute !important;
    /* Change from fixed to absolute */
    max-height: 300px !important;
    overflow-y: auto !important;
}

/* Ensure consistent height for all inputs */
.MuiInputBase-root,
.MuiButtonGroup-root,
.MuiSelect-root {
    height: 56px !important;
}

/* Adjust select input styling */
.MuiSelect-select {
    padding-right: 32px !important;
}

.MuiSelect-icon {
    right: 7px !important;
    /* Adjust the position of the dropdown icon */
}

.MuiButtonBase-root {
    z-index: 0 !important;
}

/* Ensure consistent height for all inputs */
.MuiInputBase-root,
.MuiButtonGroup-root,
.MuiSelect-root {
    height: 56px !important;
}

.MuiSelect-icon {
    right: 7px !important;
    /* Adjust the position of the dropdown icon */
}

.formScrollContainer {
    max-height: calc(85vh - 100px);
    /* Adjust based on your submit button height */
    overflow-y: auto;
    padding-right: 17px;
    /* Compensate for the scrollbar width */
    box-sizing: content-box;
}