/* FormGridStyles.css */
.formWrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    z-index: 19999;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
}

.formContainer {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    padding: 15px;
    background-color: rgba(255, 255, 255, 0.98);
    border-radius: 12px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 800px;
    max-height: 85vh;
    overflow-y: auto;
    z-index: 20000;
    animation: fadeIn 0.3s ease-out;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: scale(0.95);
    }

    to {
        opacity: 1;
        transform: scale(1);
    }
}

.formRow.fullWidth {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
}

.formRow.fullWidth>div {
    flex: 1 1 auto;
    min-width: 0;
}

.formColumn {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.roomTypePaymentMethod {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.room-type,
.payment-method {
    width: 100%;
}

.selectButton {
    width: 100%;
    height: 40px;
    font-size: 14px;
    text-align: left;
    justify-content: flex-start;
    padding: 0 12px;
}

.formColumn h3 {
    margin-bottom: 8px;
    font-size: 1rem;
    /* Increased from 15px */
    color: #333;
    /* Changed from aqua for better readability */
    font-weight: 600;
}

.formRow {
    grid-column: 1 / -1;
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
    /* Allow wrapping for smaller screens */
}

.input-wrapper {
    margin-bottom: 6px;
    width: 100%;
}

.input-wrapper .MuiFormControl-root {
    width: 100%;
}

.formRow>div {
    flex: 1 1 calc(33.333% - 10px);
    /* Distribute space evenly, accounting for gap */
    min-width: 200px;
    /* Ensure a minimum width for readability */
}

.MuiInputBase-root,
.MuiOutlinedInput-root,
.MuiSelect-root,
textarea,
.selectButton,
input[type="date"],
.MuiTextField-root {
    height: 40px !important;
    font-size: 14px !important;
    box-sizing: border-box;
}

/* Remove this specific style if it exists */
