    font-size: 13px;
}

.submit-button-container {
    grid-column: 1 / -1;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
}

.submit-button {
    width: 100%;
    max-width: 300px;
    height: 36px;
    padding: 0 16px;
    font-size: 14px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.submit-button:hover {
