}

/* Adjust the wrapper for date inputs */
.mui-date-input-wrapper {
    width: 100%;
    margin-bottom: 6px;
}

/* Style for date inputs */
input[type="date"] {
    appearance: none;
    -webkit-appearance: none;
    background-color: transparent;
    border: 1px solid rgba(0, 0, 0, 0.23);
    border-radius: 4px;
    padding: 8px 12px;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.87);
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    transition: border-color 0.2s;
}

input[type="date"]::-webkit-calendar-picker-indicator {
    color: rgba(0, 0, 0, 0.54);
    font-size: 16px;
    cursor: pointer;
}

input[type="date"]:hover {
    border-color: rgba(0, 0, 0, 0.87);
}

input[type="date"]:focus {
    outline: none;
    border-color: #1976d2;
    border-width: 2px;
    padding: 7px 11px;
    /* Adjust padding to account for thicker border */
}

/* Style for the date input wrapper */
.custom-date-input {
    position: relative;
    width: 100%;
}

.custom-date-input label {
    position: absolute;
    top: -8px;
    left: 8px;
    background-color: white;
    padding: 0 4px;
    font-size: 9px;
    color: rgba(0, 0, 0, 0.6);
    transition: color 0.2s;
}

.custom-date-input:hover label {
    color: rgba(0, 0, 0, 0.87);
}

.custom-date-input input[type="date"]:focus+label {
    color: #1976d2;
}

/* Mobile Styles */
@media screen and (max-width: 910px) {
    .formWrapper {
        align-items: flex-start;
    }

    .formContainer {
        grid-template-columns: 1fr;
        width: 100%;
        max-width: 100%;
        height: 100%;
        max-height: 100%;
        border-radius: 0;
        padding: 10px;
    }

    .formRow {
        flex-direction: column;
    }

    .submit-button {
        max-width: none;
    }

    .formRow>div {
        flex: 1 1 100%;
        /* Stack vertically on mobile */
        min-width: 0;
        /* Reset min-width for mobile */
    }
}

